function remove(key) {
  if (!key || !_has(key)) {
    return;
  }
  wx.removeStorageSync(key)
}

function clearAll() {
  wx.clearStorage()
}

function _has(key) {
  if (!key) {
    return
  }
  let value = wx.getStorageSync(key)
  if (value) {
    return true
  }
  return false
}

export default {
  get,
  all,
  set,
  remove,
  clearAll,
  has: _has
};
